.headline-container {
    position: relative;
    -webkit-transition: -webkit-transform .2s;
    transition: -webkit-transform .2s;
    -o-transition: transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s; /* Animation */
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

div.headline-container{
    max-height: 400px;
    -webkit-box-shadow: 0 4px 4px 0 var(--dark-brown-shadow),0 4px 10px 0 var(--dark-brown-shadow);
            box-shadow: 0 4px 4px 0 var(--dark-brown-shadow),0 4px 10px 0 var(--dark-brown-shadow);
}

div.headline-container:hover {
    -webkit-transform: scale(1.01);
        -ms-transform: scale(1.01);
            transform: scale(1.01);
}

.headline-text {
    position: absolute;
    top: 150px;
    left: 10px;
    right: 10px;
    color: var(--dark-brown-color);
    padding: 15px 15px;
    margin-bottom: 10px;
    background-color: var(--seashell-trans-color);
    -webkit-box-shadow: 0 4px 4px 0 var(--dark-brown-shadow),0 4px 10px 0 var(--dark-brown-shadow);
            box-shadow: 0 4px 4px 0 var(--dark-brown-shadow),0 4px 10px 0 var(--dark-brown-shadow);
}

img.headline {
    width: 100%;
    opacity: 0.3;
}

@media screen and (max-width: 800px) {
    .headline-text {
        position: absolute;
        top: 0;
        color: var(--dark-brown-color);
        padding: 15px 15px;
        margin-bottom: 10px;
        background-color: var(--seashell-trans-color);
        -webkit-box-shadow: 0 4px 4px 0 var(--dark-brown-shadow),0 4px 10px 0 var(--dark-brown-shadow);
                box-shadow: 0 4px 4px 0 var(--dark-brown-shadow),0 4px 10px 0 var(--dark-brown-shadow);
        overflow: hidden;
        -o-text-overflow: ellipsis;
           text-overflow: ellipsis;
    }
}


